@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins;
  text-decoration: none;
}

html {
  overflow-x: hidden;
}

body {
  background-color: #fff;
  position: relative;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #6e93f7;
  border-radius: 12px;
  transition: all .3s;
}

::-webkit-scrollbar-thumb:hover {
  background: #4070f4;
}

section {
  padding-top: 25px;
}

section .content {
  width: 80%;
  margin: 20px auto;
  font-family: Poppins, sans-serif;
}

section .title {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

section .title span {
  color: #0e2431;
  margin-top: 90px;
  padding-bottom: 8px;
  font-size: 30px;
  font-weight: 600;
  position: relative;
}

section .title span:before {
  content: "";
  height: 3px;
  width: 100%;
  background: #4070f4;
  position: absolute;
  bottom: 0;
  left: 0;
}

section .title span:after {
  content: "";
  height: 3px;
  width: 100%;
  width: 70%;
  background: #4070f4;
  position: absolute;
  bottom: -7px;
  left: 50%;
  transform: translateX(-50%);
}

section .topic {
  color: #0e2431;
  margin-bottom: 10px;
  font-size: 25px;
  font-weight: 500;
}

section .button {
  margin: 16px 0;
}

section .button button {
  color: #fff;
  cursor: pointer;
  background: #4070f4;
  border: 2px solid #0000;
  border-radius: 4px;
  outline: none;
  padding: 8px 16px;
  font-size: 25px;
  font-weight: 400;
  transition: all .4s;
}

section .button button:hover {
  color: #4070f4;
  background-color: #fff;
  border-color: #4070f4;
}

.center {
  text-align: center;
}

@media (max-width: 1190px) {
  section .content {
    width: 85%;
  }

  section .title span {
    margin-top: 30px;
  }
}

@media (max-width: 1050px) {
