.title, .contact__icon, .box, .article__header {
  opacity: 0;
  transition: all 1s;
}

.title.active {
  -webkit-animation: tracking-in-expand .4s cubic-bezier(.215, .61, .355, 1) both;
  animation: tracking-in-expand .3s cubic-bezier(.215, .61, .355, 1) both;
}

.article__header {
  animation: tracking-in-contract 2s cubic-bezier(.215, .61, .355, 1) both;
}

.contact__icon.active {
  animation: focus-in-contract .7s cubic-bezier(.25, .46, .45, .94) both;
}

.box.active {
  animation: text-focus-in .5s cubic-bezier(.55, .085, .68, .53) both;
}

.text-focus-in {
  animation: text-focus-in 1s cubic-bezier(.55, .085, .68, .53) both;
}

.tracking-in-expand {
  animation: tracking-in-expand .6s cubic-bezier(.215, .61, .355, 1) both;
}

@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
  }
}

.text-focus-in {
  animation: text-focus-in 1s cubic-bezier(.55, .085, .68, .53) both;
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0;
  }

  40% {
    opacity: .6;
  }

  100% {
    opacity: 1;
  }
}

.puff-in-center {
  animation: puff-in-center .7s both;
}

@keyframes puff-in-center {
  0% {
    -webkit-filter: blur(4px);
    filter: blur(4px);
    opacity: 0;
    transform: scale(2);
  }

  100% {
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
    transform: scale(1);
  }
}

.focus-in-contract {
  animation: focus-in-contract .7s cubic-bezier(.25, .46, .45, .94) both;
}

@keyframes focus-in-contract {
  0% {
    letter-spacing: 1em;
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
  }
}

.jello-horizontal {
  animation: jello-horizontal .9s both;
}

@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(1.25, .75, 1);
  }

  40% {
    transform: scale3d(.75, 1.25, 1);
  }

  50% {
    transform: scale3d(1.15, .85, 1);
  }

  65% {
    transform: scale3d(.95, 1.05, 1);
  }

  75% {
    transform: scale3d(1.05, .95, 1);
  }

  100% {
    transform: scale3d(1, 1, 1);
  }
}

.tracking-in-contract {
  animation: tracking-in-contract .8s cubic-bezier(.215, .61, .355, 1) both;
}

@keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }

  40% {
    opacity: .6;
  }

  100% {
    letter-spacing: normal;
    opacity: 1;
  }
}

