* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  counter-reset: section;
}

#article {
  margin-bottom: 0;
  padding: 100px 50px;
}

.article {
  grid-gap: 50px;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 200px;
  display: grid;
}

.article__header {
  font-size: 48px;
}

.article__paragraph {
  font-size: 21px;
}

.article:nth-child(2n+1) .article__inner {
  grid-column: 2;
}

.article:nth-child(2n) .article__inner {
  grid-column: 1;
}

.article:nth-child(2n+1) .article__inner:before {
  left: -20%;
}

.article:nth-child(2n) .article__inner:before {
  right: -20%;
}

.article__inner {
  position: relative;
}

.article__inner:before {
  counter-increment: section;
  z-index: -1;
  content: counter(section);
  color: #d3d3d3;
  font-size: 430px;
  font-weight: bold;
  position: absolute;
  top: -40%;
}

.article__inner p {
  margin: 10px;
}

@media (max-width: 900px) {
  .article {
    flex-wrap: wrap;
    flex: 100%;
    margin-bottom: 50px;
    display: flex;
  }

  .article__header {
    font-size: 38px;
  }

  .article__paragraph {
    font-size: 21px;
  }

  h2:before {
    content: counter(section) ". ";
    word-break: break-all;
    color: #d3d3d3;
    margin-right: 10px;
  }

  .article__inner:before {
    color: #0000;
  }
}

.article ul li {
  color: #fff;
