.container-komitet {
    /* max-width: 1200px; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 20px;
    .box {
        width: calc(33% - 10px);
        background: transparent;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px 30px;
        // border-radius: 5px;
    }
}

.box .quote i {
    margin-top: 10px;
    font-size: 45px;
    color: #17c0eb;
}

.container-komitet .box .image {
    margin: 10px 0;
    height: 150px;
    width: 150px;
    background: #8e44ad;
    padding: 3px;
    border-radius: 50%;
}

.box {
    .image img {
        height: 100%;
        width: 100%;
        border-radius: 50%;
        object-fit: cover;
        border: 2px solid #fff;
    }
    p {
        text-align: justify;
        margin-top: 8px;
        font-size: 16px;
        font-weight: 400;
    }
    .name_job {
        margin: 10px 0 3px 0;
        color: #8e44ad;
        font-size: 18px;
        font-weight: 600;
    }
}

.rating i {
    font-size: 18px;
    color: #8e44ad;
    margin-bottom: 5px;
}

@media (max-width: 1045px) {
    .container-komitet .box {
        width: calc(50% - 10px);
        margin-bottom: 20px;
    }
}

@media (max-width: 710px) {
    .container-komitet .box {
        width: 100%;
    }
}